  getItem() {
    this.userService.getSetting({}).subscribe(
      res => {
        if(res['result'].length>0) this.data=res['result'][0]['code']
      },
      err => {
        this.handleError(err)
      });
  }

  saveCode() {
    this.userService.deleteLocalSetting();
    this.userService.updateSetting({code:this.data}).subscribe(
      res => {
        this.handleSuccess();
        this.getItem();
      },
      err => {
        this.handleError(err)
      });
  }
  preview() {
    this.userService.setLocalSetting(this.data)
  }
  onAnchorClick() {
    this.route.fragment.subscribe(f => {
      const element = document.querySelector("#" + f)
      if (element) element.scrollIntoView({ behavior: 'smooth', block: 'start' })
    });
  }
  //when img click
