  //when input file ok button click
  selectFile(event) {
    let reader = new FileReader();
    let ff = new FormData();
    const file = event.target.files[0];
    const filename = String(Date.now())+'.' + file.name.split('.')[file.name.split('.').length -1];
    ff.append('file', file, filename);
    this.userService.uploadSetting(ff).subscribe(
      res => {
        this.handleSuccess()
        this.imgURL="/uploads/page/"+filename;
      },
      err => {
        this.handleError(err)
      }
    );
  }
  //delete Image
  deleteImage(){
    this.userService.postRequest('api/deleteFile',{path:this.imgURL},true).subscribe(
      res=>{
       this.imgURL=undefined
      },
      err=>{
        this.handleError(err)
      }
    );
  }
  //Toastr functions
  handleError(err) {
    if (err.status == 504) this.showSuccessToaster('error', '', 'server is not responsing.');
    else this.showSuccessToaster('error', '', err.error.message);
  }
  handleSuccess() {
    this.showSuccessToaster('success', '', 'success')
  }
  showSuccessToaster(style, msgtopic, msgContent) {
    this.toastr.show(style, msgtopic, msgContent);
  }
  putInMiddle(arr, key, member) {//insert memeber to in the middle of array
    const temp = [];
    for (let i = 0; i < key; i++) temp.push(arr[i])
    temp.push(member);
    for (let i = key; i < arr.length; i++) temp.push(arr[i])
    return temp;
  }
